import React,{useState} from 'react'
import './navbar.css'
import { Link } from 'gatsby'
import { htmlPrefilter } from 'jquery'

/*
Navbar
    height:40px;
*/
export default function Navbar() {
    return (
        <div className='navbar-container' onClick={(e)=>{
            let $el = document.querySelector('html')
            if($el.classList.contains('reveal-full-page')){
                $el.classList.remove('reveal-full-page')
                $el = document.querySelector('body')
                $el.classList.remove('reveal-viewport')
            }
        }}>
            <div className='navbar-head'>
                <div className='navbar-head-logo'>
                </div>
                <div className='navbar-head-text'>
                    <Link to='/'>walkerchi</Link>
                </div>
            </div>
            <div className='navbar-router'>
                <Link to='/blog'>blog</Link>
                <Link to='/'>project</Link>
                <Link to='/function'>function</Link>
                <Link to='/'>gallery</Link>
            </div>
        </div>
    )
}

